.error {
  height: 100vh;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-image: url("../img/banner.png");
    mix-blend-mode: multiply;
    z-index: -100;
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;

    &--relativ {
      mix-blend-mode: multiply;
    }

    &--abs {
      margin-left: -330px;
      margin-top: -200px;

      @include tablet {
        margin-left: -280px;
      }

      @include mobile {
        width: 170px;
        margin-left: -160px;
        margin-top: -100px;
      }
    }
  }

  // &__content {
  //   z-index: 1000;
  // }

  &__blocks {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @include small-desktop {
      grid-template-columns: repeat(1, 700px);
      gap: 30px;
      align-items: center;
      justify-content: center;
    }

    @include tablet {
      grid-template-columns: repeat(1, 500px);
    }

    @include mobile {
      grid-template-columns: repeat(1, 330px);
    }
  }

  &__title {
    padding-top: 10%;
    display: flex;
    flex-direction: column;

    margin-bottom: 30px;
    gap: 15px;

    @include tablet {
      text-align: center;
    }
  }
  &__subtitle {
    margin: 0;
    font-family: Unbounded;
    font-weight: 600;
    font-size: 40px;
    letter-spacing: 0%;
    text-transform: uppercase;
    color: white;
    padding-left: 30px;
    padding-right: 30px;

    @include mobile {
      font-size: 25px;
    }
  }

  &__red {
    background: linear-gradient(180deg, #ff0000 0%, #af0000 100%);
    border-radius: 30px;
    padding-top: 15px;
    padding-bottom: 15px;
  }

  &__text {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-bottom: 30px;
  }

  &__descr {
    max-width: 500px;
    margin: 0;
    font-family: Inter;
    font-weight: 500;
    font-size: 36px;
    line-height: 130%;
    color: white;

    @include mobile {
      font-size: 20px;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 30px;
    border: 3.19px solid;
    border-radius: 30px;
    border: 3.19px solid;
    border-color: rgba(255, 248, 248, 0.7);
    background-color: #83b3d3;

    &:hover {
      background-color: #30875a;
    }
  }

  &__link {
    font-family: Inter;
    font-weight: 500;
    font-size: 23px;
    line-height: 100%;
    text-align: center;
    color: white;

    & > img {
      margin-right: 10px;
    }
  }
}
